* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  margin-top: 0;
  padding-left: 0;
  font-size: 20px;
  color: gray;
  list-style: none;
}
.country-list .list-item span {
  margin-right: 10px;
}
.country-info span {
  font-size: 30px;
  margin-right: 20px;
}
